import React from "react";
import { Form, Button, Col } from "react-bootstrap";
import { useForm } from "react-hook-form";

const Comment = (props: any) => {
  const { register, handleSubmit } = useForm();

  return (
    <Form onSubmit={handleSubmit(props.handleCommentAdd)} className="comm-form">
      <Form.Row>
        <Form.Group as={Col}>
          <Form.Label>
             
            <Form.Control
              type="text"
              placeholder=""
              {...register("password")}
              defaultValue={""}
            />
          </Form.Label>
          <Form.Control
            type="text"
            as="textarea"
            rows={3}
            placeholder=""
            {...register("desc")}
            defaultValue={""}
          />
        </Form.Group>
      </Form.Row>
      <Form.Row>
        <Col>
          <Button
            className="float-right"
            variant="dark"
            type="submit"
            size="sm"
          >
            
          </Button>
        </Col>
      </Form.Row>
    </Form>
  );
};

export default Comment;
